  <template>
  <div class="container">
    <div v-if="!gameFinished">
      <h1>Su Tasarrufu Oyunu</h1>

      <div class="question">{{ questions[currentIndex].text }}</div>

      <div class="buttons">
        <button class="correct" @click="checkAnswer(true)">Doğru</button>
        <button class="wrong" @click="checkAnswer(false)">Yanlış</button>
      </div>

      <div class="feedback" v-if="showFeedback">
        {{ feedbackMessage }}
      </div>

      <div class="score">
        Soru {{ currentIndex + 1 }} / {{ questions.length }} — Puan: {{ score }}
      </div>
    </div>

    <div v-else>
      <h1>Tebrikler!</h1>
      <p>Oyunu bitirdin. Toplam Puanın: {{ score }} / {{ questions.length }}</p>
      <button @click="restartGame">Tekrar Oyna</button>
    </div>
  </div>
  </template>

  <script>
  import questions from './questions.js';
  import correctSound from './assets/correct.mp3';
  import wrongSound from './assets/wrong.mp3';



  export default {
  data() {
    return {
    originalQuestions: questions,  // orijinal liste
    questions: [],                 // karıştırılmış liste
    currentIndex: 0,
    score: 0,
    showFeedback: false,
    feedbackMessage: ""
    };
  },
  mounted() {
  this.questions = this.shuffle([...this.originalQuestions]);
  },
  methods: {
    shuffleArray(array) {
    for (let i = array.length - 1; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1));
      [array[i], array[j]] = [array[j], array[i]];
    }
    return array;
    },


    checkAnswer(userAnswer) {
    const correct = this.questions[this.currentIndex].answer === userAnswer;
    this.feedbackMessage = correct ? "✅ Doğru!" : "❌ Yanlış!";
    if (correct) {
      this.score++;
      this.playSound("correct");
    } else {
      this.playSound("wrong");
    }

    this.showFeedback = true;
    setTimeout(() => {
      this.showFeedback = false;
      this.nextQuestion();
    }, 1500);
    },
    nextQuestion() {
      if (this.currentIndex < this.questions.length - 1) {
        this.currentIndex++;
      } else {
        this.gameFinished = true;
      }
    },
    playSound(type) {
    const sound = new Audio(type === 'correct' ? correctSound : wrongSound);
    sound.play();
    },
    restartGame() {
      this.currentIndex = 0;
      this.score = 0;
      this.gameFinished = false;
    }
  }
  }
  </script>

